<template>
  <div class="login-container">
    <div class="login-box">
      <!--  -->
      <div class="avater-box">
        <img src="../assets/logo.png" alt="" />
      </div>
      <!--  -->
      <el-form ref="loginFormRef" :rules="rules" :model="loginForm" label-width="0px" class="login-from" >
        <!--  -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input show-password prefix-icon="iconfont icon-3702mima"  v-model="loginForm.password"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="restLoginFrom"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      /*  */
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 8, message: ' 3  5 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 10, message: ' 3  5 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    /*  */
    restLoginFrom () {
      this.$refs.loginFormRef.resetFields()
    },
    /*  */
    login () {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        /*  */
        const { data: res } = await this.$http.post('login', this.loginForm)
        console.log(res)
        /* 200  */
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        /* token */
        window.sessionStorage.setItem('token', res.data.token)
        /*  */
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login-container {
  height: 100%;
  background-color: #2b4b6b;
  .login-box {
    width: 450px;
    height: 300px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .avater-box {
    width: 130px;
    height: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
  .login-from{
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    .btns{
    display: flex;
    justify-content: flex-end;
  }
  }
}
</style>
