<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert
        show-icon
        :closable="false"
        title=":"
        type="warning"
      >
      </el-alert>
      <!--  -->
      <el-row class="cat_opt">
        <el-col>
          <span>: </span>
          <!--  -->
          <el-cascader
            expand-trigger="hover"
            v-model="selectedCateKeys"
            :options="catelist"
            :props="cateprops"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tabs -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="" name="many">
          <!--  -->
          <el-button
            @click="addDialogVisible = true"
            :disabled="isBtnDisabled"
            type="primary"
            size="mini"
            ></el-button
          >
          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  @close="removeTag(i,scope.row)"
                  closable
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  >{{ item }}</el-tag
                >
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label=""
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  @click="showEditParams(scope.row.attr_id)"
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  ></el-button
                >
                <el-button
                  @click="deleteParams(scope.row.attr_id)"
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <!--  -->
          <el-button
            @click="addDialogVisible = true"
            :disabled="isBtnDisabled"
            type="primary"
            size="mini"
            ></el-button
          >
          <!--  -->
          <el-table :data="onlyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
                <template slot-scope="scope">
                <el-tag
                  @close="removeTag(i,scope.row)"
                  closable
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  >{{ item }}</el-tag
                >
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label=""
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  @click="showEditParams(scope.row.attr_id)"
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  ></el-button
                >
                <el-button
                  @click="deleteParams(scope.row.attr_id)"
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog
      :title="'' + titelText"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="resetAddRules"
    >
      <!--  -->
      <el-form
        :model="addParamsForm"
        :rules="addParamsRules"
        ref="addParamsRef"
        label-width="100px"
      >
        <el-form-item :label="titelText" prop="attr_name">
          <el-input v-model="addParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="'' + titelText"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="resetEditRules"
    >
      <!--  -->
      <el-form
        :model="editParamsForm"
        :rules="addParamsRules"
        ref="editParamsRef"
        label-width="100px"
      >
        <el-form-item :label="titelText" prop="attr_name">
          <el-input v-model="editParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      catelist: [],
      // 
      cateprops: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      selectedCateKeys: [],
      // tabs
      activeName: 'many',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      addDialogVisible: false,
      // 
      editDialogVisible: false,
      // 
      addParamsForm: {
        attr_name: ''
      },
      // 
      editParamsForm: {
        attr_name: ''
      },
      // 
      addParamsRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    // 
    this.getCateGoriesData()
  },
  methods: {
    // 
    async getCateGoriesData () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.catelist = res.data
    },
    // 
    handleChange () {
      this.getParamsData()
    },
    // 
    async getParamsData () {
      // 
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      // 
      const { data: res } = await this.$http.get(
        `categories/${this.threeCateId}/attributes`,
        { params: { sel: this.activeName } }
      )
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 
        item.inputVisible = false
        // 
        item.inputValue = ''
      })
      if (this.activeName === 'many') {
        // 
        this.manyTableData = res.data
      } else {
        // 
        this.onlyTableData = res.data
      }
    },
    // tabs
    handleTabClick () {
      this.getParamsData()
    },
    // 
    resetAddRules () {
      this.$refs.addParamsRef.resetFields()
    },
    // 
    addParams () {
      this.$refs.addParamsRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          `categories/${this.threeCateId}/attributes`,
          {
            attr_name: this.addParamsForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg)
        }
        this.$message.success(res.meta.msg)
        this.addDialogVisible = false
        this.getParamsData()
      })
    },
    // 
    async showEditParams (id) {
      // 
      const { data: res } = await this.$http.get(
        `categories/${this.threeCateId}/attributes/${id}`,
        { attr_sel: this.activeName }
      )
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.editParamsForm = res.data
      this.editDialogVisible = true
    },
    // 
    resetEditRules () {
      this.$refs.editParamsRef.resetFields()
    },
    // 
    editParams () {
      this.$refs.editParamsRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          `categories/${this.threeCateId}/attributes/${this.editParamsForm.attr_id}`,
          {
            attr_name: this.editParamsForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        }
        this.$message.success(res.meta.msg)
        this.getParamsData()
        this.editDialogVisible = false
      })
    },
    // 
    async deleteParams (id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // 
      const { data: res } = await this.$http.delete(
        `categories/${this.threeCateId}/attributes/${id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.$message.success(res.meta.msg)
      this.getParamsData()
    },
    // 
    async  handleInputConfirm (row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        // 
        row.inputVisible = false
      }
      // 
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      // 
      row.inputVisible = false
      // ,
      this.saveAttrVals(row)
    },
    // 
    showInput (row) {
      row.inputVisible = true
      // 
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // attr_vals
    async saveAttrVals (row) {
      const { data: res } = await this.$http.put(`categories/${this.threeCateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.$message.success(res.meta.msg)
    },
    // tag
    removeTag (i, row) {
      row.attr_vals.splice(i, 1)
      this.saveAttrVals(row)
    }
  },
  computed: {
    // true
    isBtnDisabled () {
      if (this.selectedCateKeys.length !== 3) {
        return true
      }
      return false
    },
    // id
    threeCateId () {
      if (this.selectedCateKeys.length === 3) {
        return this.selectedCateKeys[2]
      }
      return null
    },
    // 
    titelText () {
      if (this.activeName === 'many') {
        return ''
      }
      return ''
    }
  }
}
</script>

<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}
.el-tag {
  margin: 10px;
}
.button-new-tag,
.input-new-tag{
    margin: 10px;
    width: 120px;
}
</style>
