<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="10"
          ><el-input v-model="goodsListInfo.query" placeholder="" clearable @clear="getGoodsList">
            <el-button
              @click="getGoodsList"
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input
        ></el-col>
        <!--  -->
        <el-col :span="4">
          <el-button  type="primary" @click="goAddGoodsList"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table  :data="goodsList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="()" width="95px"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="70px"></el-table-column>
        <el-table-column prop="add_time" label="" width="140px">
            <template slot-scope="scope">
                {{scope.row.add_time | dateFormat}}
            </template>
        </el-table-column>
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"
              ></el-button
            >
            <el-button @click="removeGoods(scope.row.goods_id)" type="danger" icon="el-icon-delete" size="mini"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="goodsListInfo.pagenum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="goodsListInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      goodsList: [],
      // 
      goodsListInfo: {
        query: '',
        pagenum: 1, // 
        pagesize: 10 // 
      },
      // 
      total: 0
    }
  },
  created () {
    // 
    this.getGoodsList()
  },
  methods: {
    // 
    async getGoodsList () {
      const { data: res } = await this.$http.get('goods', {
        params: this.goodsListInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.goodsList = res.data.goods
      this.total = res.data.total
    },
    // 
    handleSizeChange (newSize) {
      this.goodsListInfo.pagesize = newSize
      this.getGoodsList()
    },
    // 
    handleCurrentChange (newPage) {
      this.goodsListInfo.pagenum = newPage
      this.getGoodsList()
    },
    // 
    async  removeGoods (id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete('goods/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.getGoodsList()
      this.$message.success(res.meta.msg)
    },
    // 
    goAddGoodsList () {
      this.$router.push('/goods/addgoods')
    }
  }
}
</script>

<style lang="less" scoped>
.el-pagination{
    margin-left: 12.5%;
}
</style>
