<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="10">
          <el-input
            v-model="queryInfo.query"
            clearable
            @clear="getOrderList"
            placeholder=""
          >
            <el-button
              @click="getOrderList"
              slot="append"
              icon="el-icon-search"
            ></el-button> </el-input
        ></el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column
          prop="order_price"
          label=""
          width="70"
        ></el-table-column>
        <el-table-column prop="order_pay" label="" width="80">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.order_pay === '1'"
              ></el-tag
            >
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="is_send"
          label=""
          width="70"
        ></el-table-column>
        <el-table-column prop="create_time" label="" width="140">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="">
            <el-button
              @click="showEditOrder"
              type="primary"
              icon="el-icon-edit"
              size="mini"
              ></el-button
            >
            <el-button
              @click="showProgressBox"
              type="success"
              icon="el-icon-location-outline"
              size="mini"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="edityDialogVisible"
        width="50%"
        @close="closeEditDialog"
      >
        <el-form
          :model="editForm"
          :rules="editFormRules"
          ref="editFormRef"
          label-width="100px"
        >
          <el-form-item label="/" prop="address1">
            <el-cascader
              :options="cityData"
              v-model="editForm.address1"
            ></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address2">
            <el-input v-model="editForm.address2"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="edityDialogVisible = false"> </el-button>
          <el-button type="primary" @click="edityDialogVisible = false"
            > </el-button
          >
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="progressVisible"
        width="50%"
      >
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import cityData from './citydata.js'
export default {
  data () {
    return {
      // 
      orderList: [],
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      total: 0,
      // 
      edityDialogVisible: false,
      editForm: {
        address1: [],
        address2: ''
      },
      editFormRules: {
        address1: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        address2: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      cityData,
      // 
      progressVisible: false
    }
  },
  created () {
    // 
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList () {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.orderList = res.data.goods
      this.total = res.data.total
    },
    // 
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    // 
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    showEditOrder () {
      this.edityDialogVisible = true
    },
    // 
    closeEditDialog () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    async  showProgressBox () {
      // 
      this.progressVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
</style>
