<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRoles"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="rolesList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scoped">
            <el-row
              :class="[
                'dbbottom',
                i1 === 0 ? 'dbtop' : '',
                'vcenter',
                'vmargin',
              ]"
              v-for="(item1, i1) in scoped.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag @close="removeRightById(scoped.row, item1.id)" closable>
                  {{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!-- for -->
                <el-row
                  :class="[i2 === 0 ? '' : 'dbtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      @close="removeRightById(scoped.row, item2.id)"
                      closable
                      type="success"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag
                      @close="removeRightById(scoped.row, item3.id)"
                      closable
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button
              @click="editRoles(scope.row.id)"
              type="primary"
              icon="el-icon-edit"
              size="mini"
              ></el-button
            >
            <el-button
              @click="deleteRoles(scope.row.id)"
              type="danger"
              icon="el-icon-delete"
              size="mini"
              ></el-button
            >
            <el-button
              @click="showSetRightDialog(scope.row)"
              type="warning"
              icon="el-icon-setting"
              size="mini"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addRolesDialogVisible"
        width="50%"
        @close="addRolesClose"
      >
        <!--  -->
        <el-form
          :model="addRoledsForm"
          :rules="addRolesRules"
          ref="addRolesRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="roleName">
            <el-input v-model="addRoledsForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="addRoledsForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addRolesDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addRolesFn"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editRolesDialogVisible"
        width="50%"
        @close="editRolesClose"
      >
        <!--  -->
        <el-form
          :model="editRoledsForm"
          :rules="addRolesRules"
          ref="editRolesRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="roleName">
            <el-input v-model="editRoledsForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="editRoledsForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editRolesDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editRolesFn"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="setRightDialogVisible"
        width="50%"
        @close="setRightDialogClosed"
      >
      <!--  -->
        <el-tree ref="treeRef" :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys"></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRightDialogVisible = false"> </el-button>
          <el-button type="primary" @click="allRights"
            > </el-button
          >
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      rolesList: [],
      // 
      rightsList: [],
      // 
      addRolesDialogVisible: false,
      // 
      editRolesDialogVisible: false,
      // 
      setRightDialogVisible: false,
      // 
      addRoledsForm: {
        roleName: '',
        roleDesc: ''
      },
      // 
      addRolesRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 2,
            max: 15,
            message: ' 2  15 ',
            trigger: 'blur'
          }
        ]
      },
      // id
      editRoledsForm: {},
      // 
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      // id
      defKeys: [],
      // id
      roleId: ''
    }
  },
  created () {
    // 
    this.getRolesList()
  },
  methods: {
    // 
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rolesList = res.data
    },
    // id
    async removeRightById (roles, rightId) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(
        `roles/${roles.id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      roles.children = res.data
    },
    // 
    addRoles () {
      this.addRolesDialogVisible = true
    },
    // 
    async editRoles (id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editRoledsForm = res.data
      this.editRolesDialogVisible = true
    },
    // 
    addRolesClose () {
      this.$refs.addRolesRef.resetFields()
    },
    // 
    editRolesClose () {
      this.$refs.editRolesRef.resetFields()
    },
    // 
    addRolesFn () {
      // 
      this.$refs.addRolesRef.validate(async (valid) => {
        if (!valid) return
        // 
        const { data: res } = await this.$http.post(
          'roles',
          this.addRoledsForm
        )
        if (res.meta.status !== 201) {
          this.$message.error('')
        }
        this.addRolesDialogVisible = false
        this.getRolesList()
        this.$message.success('')
      })
    },
    // 
    editRolesFn () {
      // 
      this.$refs.editRolesRef.validate(async (valid) => {
        if (!valid) return
        // 
        const { data: res } = await this.$http.put(
          'roles/' + this.editRoledsForm.id,
          {
            roleName: this.editRoledsForm.roleName,
            roleDesc: this.editRoledsForm.roleDesc
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.editRolesDialogVisible = false
        this.getRolesList()
        this.$message.success('')
      })
    },
    // 
    async deleteRoles (id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      // 
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // ,
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList()
    },
    // 
    async  showSetRightDialog (role) {
      // id
      this.roleId = role.id
      // 
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rightsList = res.data
      this.getLeafKeys(role, this.defKeys)
      this.setRightDialogVisible = true
    },
    // id,defKeys
    getLeafKeys (node, arr) {
      // nodechildren
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item =>
        this.getLeafKeys(item, arr))
    },
    // iddefKeys
    setRightDialogClosed () {
      this.defKeys = []
    },
    // 
    async  allRights () {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.dbtop {
  border-top: 1px solid #eee;
}
.dbbottom {
  border-bottom: 1px solid #eee;
}
.vmargin {
  margin-left: 40px;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
