<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert
        title=""
        type="info"
        show-icon
        center
        :closable="false"
      ></el-alert>
      <!--  -->
      <el-steps
        :space="200"
        :active="activeIndex - 0"
        finish-status="success"
        align-center
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!-- tabs -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          v-model="activeIndex"
          :tab-position="'left'"
          :before-leave="beforeTabLeave"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <!--  -->
              <el-cascader
                expand-trigger="hover"
                v-model="addForm.goods_cat"
                :options="catelist"
                :props="cateProps"
                @change="handleChange"
              >
              </el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item
              :label="item.attr_name"
              v-for="item in manyTableData"
              :key="item.attr_id"
            >
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  border
                  v-for="(cb, i) in item.attr_vals"
                  :key="i"
                  :label="cb"
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTableData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              action="http://127.0.0.1:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headerObj"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <!--  -->
            <el-button type="primary" class="btnAdd" @click="add"
              ></el-button
            >
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="imgDialogVisible" width="50%">
      <img class="imgdialog" :src="previewPath" alt="" />
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data () {
    return {
      // 
      activeIndex: '0',
      // 
      addForm: {
        goods_name: '',
        goods_price: '',
        goods_weight: '',
        goods_number: '',
        // 
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: '',
        attrs: []
      },
      // 
      addFormRules: {
        goods_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_price: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_weight: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_number: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_cat: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      // 
      catelist: [],
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      previewPath: '',
      // 
      imgDialogVisible: false
    }
  },
  created () {
    // 
    this.getCateList()
  },
  methods: {
    // 
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.catelist = res.data
    },
    // 
    handleChange () {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    // tabs
    beforeTabLeave (activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    // tabs
    async tabClicked () {
      // 
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.threeCateId}/attributes`,
          { params: { sel: 'many' } }
        )
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        }
        res.data.forEach((item) => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.manyTableData = res.data
      } else if (this.activeIndex === '2') {
        // 
        const { data: res } = await this.$http.get(
          `categories/${this.threeCateId}/attributes`,
          { params: { sel: 'only' } }
        )
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        }
        this.onlyTableData = res.data
      }
    },
    // 
    handlePreview (file) {
      this.previewPath = file.response.data.url
      this.imgDialogVisible = true
      // console.log(file)
    },
    // 
    handleRemove (file) {
      // 
      const filePath = file.response.data.tmp_path
      // 
      const i = this.addForm.pics.findIndex((x) => x.pic === filePath)
      // 
      this.addForm.pics.splice(i, 1)
      // console.log(this.addForm)
    },
    // 
    handleSuccess (response) {
      const picInfo = { pic: response.data.tmp_path }
      this.addForm.pics.push(picInfo)
      // console.log(this.addForm)
    },
    // 
    add () {
      // 
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) {
          return this.$message.error('')
        }
        // 
        const form = _.cloneDeep(this.addForm) /*  */
        form.goods_cat = form.goods_cat.join(',')
        // 
        this.manyTableData.forEach((item) => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          }
          this.addForm.attrs.push(newInfo)
        })
        this.onlyTableData.forEach((item) => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        form.attrs = this.addForm.attrs
        console.log(form)
        //  
        const { data: res } = await this.$http.post('goods', form)
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg)
        }
        this.$message.success(res.meta.msg)
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    threeCateId () {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.imgdialog {
  width: 100%;
}
.btnAdd {
  margin-top: 15px;
}
</style>
