<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addCateGories"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <tree-table
        class="treeTable"
        :data="categoriesData"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
      >
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            style="color: lightgreen"
            v-if="scope.row.cat_deleted === false"
          ></i>
          <i class="el-icon-error" v-else style="color: red"></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag
            size="mini"
            type="success"
            v-else-if="scope.row.cat_level === 1"
            ></el-tag
          >
          <el-tag size="mini" type="warning" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button
            @click="editCate(scope.row.cat_id)"
            type="primary"
            icon="el-icon-edit"
            size="mini"
            ></el-button
          >
          <el-button
            @click="deleteCate(scope.row.cat_id)"
            type="danger"
            icon="el-icon-delete"
            size="mini"
            ></el-button
          >
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="querInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="querInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      @close="addCateClose"
      title=""
      :visible.sync="cateDialogVisible"
      width="50%"
    >
      <!--  -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
      >
        <el-form-item label=":" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-cascader
            change-on-select
            clearable
            v-model="selectedKeys"
            expand-trigger="hover"
            :options="fatherCateList"
            :props="cascaderProps"
            @change="parentCateChanged"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editCateDialogVisible"
      width="50%"
      @close="resetEditCateRules"
    >
      <el-form
        :model="editCateForm"
        :rules="editCateRules"
        ref="editCateRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editCateForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="finishEditCate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      categoriesData: [],
      // 
      querInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 
      total: 0,
      // table
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          type: 'template',
          template: 'isok'
        },
        {
          label: '',
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          type: 'template',
          template: 'opt'
        }
      ],
      // 
      cateDialogVisible: false,
      // 
      addCateForm: {
        cat_name: '',
        // id
        cat_pid: 0,
        // 
        cat_level: 0
      },
      // 
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      fatherCateList: [],
      // 
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // id
      selectedKeys: [],
      // 
      editCateDialogVisible: false,
      // id
      editCateForm: [],
      editCateRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    // 
    this.getCateGoriesData()
  },
  methods: {
    // 
    async getCateGoriesData () {
      const { data: res } = await this.$http.get('categories', {
        params: this.querInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.categoriesData = res.data.result
      this.total = res.data.total
    },
    // pagesize
    handleSizeChange (newSize) {
      this.querInfo.pagesize = newSize
      this.getCateGoriesData()
    },
    // pagenum
    handleCurrentChange (newPage) {
      this.querInfo.pagenum = newPage
      this.getCateGoriesData()
    },
    // 
    addCateGories () {
      // 
      this.getFatherCate()
      this.cateDialogVisible = true
    },
    // 
    async getFatherCate () {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.fatherCateList = res.data
    },
    // 
    parentCateChanged () {
      if (this.selectedKeys.length > 0) {
        // 
        this.addCateForm.cat_pid =
          this.selectedKeys[this.selectedKeys.length - 1]
        this.addCateForm.cat_level = this.selectedKeys.length
      } else {
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    // 
    addCate () {
      this.$refs.addCateFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          'categories',
          this.addCateForm
        )
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getCateGoriesData()
        // 
        this.cateDialogVisible = false
      })
    },
    // 
    addCateClose () {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    },
    // 
    async deleteCate (id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      //  confirm
      // cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // 
      const { data: res } = await this.$http.delete('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getCateGoriesData()
    },
    // 
    async editCate (id) {
      // 
      const { data: res } = await this.$http.get('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editCateForm = res.data
      this.editCateDialogVisible = true
    },
    // 
    resetEditCateRules () {
      this.$refs.editCateRef.resetFields()
    },
    // 
    finishEditCate () {
      // 
      this.$refs.editCateRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          'categories/' + this.editCateForm.cat_id,
          {
            cat_name: this.editCateForm.cat_name
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // 
        this.editCateDialogVisible = false
        // 
        this.getCateGoriesData()
        this.$message.success('')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-pagination {
  margin-left: 5px;
}
.treeTable {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;
}
</style>
